import React, { useEffect } from 'react';
import HomeIcon from '@mui/icons-material/Home';
import SensorsIcon from '@mui/icons-material/Sensors';
import SettingsIcon from '@mui/icons-material/Settings';
import AutoFixNormalIcon from '@mui/icons-material/AutoFixNormal';
import { Settings } from '@mui/icons-material';

import "./Navbar.css"
import "../../styles/globals.css"

const BASE_URL = "/TechHome-Repository/#"

const Link = ({ to, children, ...rest }) => {
  return <a href={to} {...rest}>{children}</a>; // Simplified Link
};

function DetectClickOrTouch(event) {
  // Move document-related logic here inside the event handler, so it only runs in the browser
  let currentActiveItem = document.getElementsByClassName("active")[0];
  if (currentActiveItem) {
    currentActiveItem.classList.remove("active");
  }

  let parent = event.target.closest(".nav-item");
  if (parent) {
    parent.classList.add("active");
  }
}

export default function Navbar(props) {
  useEffect(() => {
    // You can safely use document inside useEffect since it's executed in the browser
    let NavLinks = document.getElementsByClassName("nav-link");
    for (let i = 0; i < NavLinks.length; i++) {
      NavLinks[i].addEventListener('click', DetectClickOrTouch);
    }

    // Clean up event listeners on unmount
    return () => {
      for (let i = 0; i < NavLinks.length; i++) {
        NavLinks[i].removeEventListener('click', DetectClickOrTouch);
      }
    };
  }, []); // Empty array ensures this effect runs once when the component mounts

  return (
    <nav className="navbar">
      <ul className="navbar-nav">
        <li className={`nav-item ${props.elementActive === "home" ? "active" : ""}`}>
          <Link to={`${BASE_URL}`} className="nav-link">
            <HomeIcon className="primary" />
            <span className="link-text">Início</span>
          </Link>
        </li>
        <li className={`nav-item ${props.elementActive === "sensors" ? "active" : ""}`}>
          <Link to={`${BASE_URL}/sensors`} className="nav-link">
            <SensorsIcon className="primary" />
            <span className="link-text">Sensores</span>
          </Link>
        </li>
        <li className={`nav-item ${props.elementActive === "actions" ? "active" : ""}`}>
          <Link to={`${BASE_URL}/actions`} className="nav-link">
            <AutoFixNormalIcon className="primary" />
            <span className="link-text">Macros</span>
          </Link>
        </li>
        <li className={`nav-item ${props.elementActive === "configuration" ? "active" : ""}`}>
          <Link to={`${BASE_URL}/configuration`} className="nav-link">
            <SettingsIcon className="primary" />
            <span className="link-text">Configurações</span>
          </Link>
        </li>
      </ul>
    </nav>
  );
}
